import React from 'react'
import './BasketItem.css'

const BasketItem = (props) => {
    return(
        <div className="basketItem">
            <div>
                <img className="basketItemImg" alt="" src={props.item.photoSrc}/>
                <p className="basketItemName">{props.item.brand} {props.item.model}</p>
                <p className="basketItemCost">{props.item.cost}$</p>
            </div>
            <span style={{
                    backgroundImage: "url(./img/x.png)"
                  }}
                  onClick={() => props.deleteProductFromBasket(props.item.id, props.item.cost)}></span>
        </div>
    )
}

export default BasketItem