import React from 'react'
import './ProductViewer.css'

const ProductViewer = (props) => {
    return (
        <div>
            <div className="productViewerWrap">
                <div className="productViewerImgWrap" style={{backgroundImage: "url(" + props.product.photoSrc + ")"}}>
                </div>
                <div className="productViewerInfo">
                    <div className="productViewerExitWrap" onClick={() => props.setShowingViewer()}>
                        <span className="productViewerExit">X</span>
                    </div>
                    <p className="productViewerText">{props.product.brand} {props.product.model}</p>
                    <p className="productViewerText">{props.product.cost}$</p>
                    <button className="productViewerButton" onClick={() => props.addProductToBasket(props.product)}>Add to Basket</button>
                </div>
            </div>
        </div>
    )
}

export default ProductViewer